.App {
  text-align: center;
  /* margin: 20px; */
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

::-webkit-scrollbar {
  display: none;
}

.section {
  min-height: 50vh;
  max-width: 63vw;
  border: solid thin #282c34;
  justify-self: right;
  padding: 12px 50px;
  text-align: start;
  border-radius: 10px;
  margin: 50px;
}

.floating_section {
  min-height: 50vh;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
